export default {
  async fetch() {
    const { data } = await this.$axios.get("/blogs?_limit=-1");
    const { data: banner } = await this.$axios.get("/blog-banner");

    data.sort((a, b) => {
        return new Date(b.created_at) - new Date(a.created_at);
      });

    this.banner = banner.image ? banner.image.url : "";
    this.posts = data;
  },
  data() {
    return {
      posts: [],
      banner: "",
    };
  },
};
